@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@400;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Spartan', sans-serif;
    
    
}
.App {
    max-width: 1440px;
    margin: 0 auto;
    overflow-x: hidden;
}
@media screen and (min-width: 375px) {
    .App {
        max-width: 1110px;
        
    }
}
@media screen and (min-width: 768px) {
    .App {
        max-width: 1440px;
        
    }
}