@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@400;700&display=swap);
.imgfilter{-webkit-filter:brightness(60%);filter:brightness(60%)}.footerlogo-container{background-color:#1b1d23}
*{margin:0;padding:0;box-sizing:border-box;font-family:"Spartan",sans-serif}.App{max-width:1440px;margin:0 auto;overflow-x:hidden}@media screen and (min-width: 375px){.App{max-width:1110px}}@media screen and (min-width: 768px){.App{max-width:1440px}}
.close-icon{display:none}.hamburger.active .close-icon{display:block}.hamburger.active .hamburger-icon{display:none}@media screen and (max-width: 768px){.nav-menu{position:fixed;top:-100%;background-color:#eeeff4;z-index:1000;transition:.4s all ease}}.nav-menu.active{display:block;top:104px;right:0;width:90vw;height:20vh;display:flex;flex-direction:column;padding:20px;transition:.4s all ease}.nav-links{padding-bottom:20px;position:relative}.nav-links:hover{color:#000}@media screen and (min-width: 768px){.nav-links:after{content:"";position:absolute;bottom:0;left:30%;width:0;height:2px;background-color:#000;opacity:.6;transition:width .3s ease 0s,opacity .3s ease 0s}.nav-links:hover:after{color:#000;width:50%;opacity:.9}}.nav-list-dropped{display:none}.nav-items{padding:10px}.nav-items.active{margin-left:10px}
form input,textarea{width:100%;border:none;outline:none;border-bottom:2px solid #c8ccd8;resize:none}@media screen and (min-width: 1240px){form input,textarea{width:800px}}input,textarea{padding:20px}
